import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {HeaderInfo, Menus} from '../app.config';
import {IbukiService} from '../services/ibuki.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  headerData: any;
  subs : any;
  menus : any;
  showMenu: boolean = false;
  selectedMenu: any;
  brand: any = "Casio";
  
  constructor(private router: Router, private activatedRoute: ActivatedRoute ,private ibuki: IbukiService) { }  //

  ngOnInit() {

    //Getting the brand selected by the user
    this.subs = this.ibuki.behFilterOn('brand').subscribe(d => {
      if (d.error) {
        console.log(d.error);
      } else {
        this.brand = d.data;
      }
    });
    
    const sub1 = this.ibuki.filterOn('showMenu').subscribe(d => {
      if (d.error) {
        console.log(d.error);
      } else {
        this.showMenu = d.data
      }
    });
    this.subs.add(sub1);

    const sub2 = this.ibuki.filterOn('selectedMenu').subscribe(d => {
      if (d.error) {
        console.log(d.error);
      } else {
        this.selectedMenu = d.data
      }
    });
    this.subs.add(sub2);

    this.headerData = HeaderInfo;
    this.menus = Menus;

  }

  navigate(menu)
  {
    this.selectedMenu = menu;
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

}
